.frames{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 10px;
}
.stockCounter input {
    border: none;
    width: 3rem;
    text-align: center;
  }
  
  .plus,
  .minus {
    padding: 0.1rem 0.5rem;
  }
  
  .stockCounter input::-webkit-outer-spin-button,
  .stockCounter input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  
  .form-control:disabled,
  .form-control[readonly] {
    background-color: white;
  }
  .parah{
    line-height:30px ;
  }
  .font{
    font-size: 24px;
  }
  .detailbutton{
    background-color: #836CA9 !important;
    color: white;
  }
  .carttitle{
    font-size: 20px;
    text-decoration: none;
  }
  .About-section{
    display: flex;
    padding: 80px 0px 0px;
    flex-wrap: wrap;
}
.image-sections{
    position: relative;
    width: 459.2px;
    padding: 5px 20px;
    height: 520px;
    z-index: -1;
}
.sectionimage{
    width: 180px;
    height: 430px;
    box-shadow: 4px 4px gray;
    box-sizing: border-box;

}
.sectionimage1{
    position: absolute;
    width: 180px;
    height: 430px;
    top: 44px;
    left: 200px;
    box-shadow: 4px 4px gray;
    box-sizing: border-box;

}
.content-section{
    width: 500px;
    margin-left: 50px;

}
.content-section h2{
    
    font-size: 48px;
    margin-bottom: 40px;
}
.content-section p{
    font-size: 18px;
    text-indent: 38px;
    font-weight: 600;
}
.content-section li{
    line-height: 1.5;
    font-size: 18px;
    margin: 10px
}
.button{
    background-color: black;
    color: white;
    font-size: 24px;
    padding: 10px 20px;
    border-radius: 10px;
    margin-top: 15px;
    cursor: pointer;   
}
.button-viewall{
  margin-top: 20px;
  color: white;
  background-color: #836CA9;
  font-weight: 500;
  font-size: 20px;

}
.image{
  height: 800px !important;
  width: 100% !important;
}