*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    padding-top: 56px;
}
.navbar{
    background-color: #836CA9 !important;
}
.navlink{
    font-weight: 400 !important;
    font-size: 20px;
    color: white !important;
    text-decoration: none;
    line-height: 45px !important;
    margin-left: 15px;
}
.nav-icon{
    font-size: 24px;
    color: white;
    font-weight: 100;
}
.navicon{
    color: white !important;
    font-size: 24px;
    
}
.navlink:hover{
    border-bottom: 2px solid blueviolet;
    color: black !important;
}
.nav-icon:hover{
    color: rgb(3, 3, 3);
    cursor: pointer;
}
.footer-parah p{
    font-size: 12px;
    margin-left: 5px;
}
.inputelements{
    border: 2px solid rgb(103, 102, 102) !important;
}
.fgpwd{
    text-decoration: underline;
    color: black;
}
.acc-btn{
    background-color: #836CA9 !important;
    color: white !important;
    width: 120px;
  
    font-size: 20px !important;
    font-weight: 400 !important;
    
}
.paragraph{
    color: #4D4D4D;
    font-size: 17px;
    color: #4D4D4D;
    text-align: justify;
    font-weight: 500;
}
.link{
    color: #4D4D4D; ;
    text-decoration: none;
}
.link:hover{
    color: black;
    text-decoration: underline;

}
.shop-text{
    color: black !important;
    font-size: 18px;
}
.cart-img{
    height: 150px;
}
.pillsize{
    font-size: 10px !important;
    background-color: #13022f !important;
}
.quantity-box{
    border:2px solid #4D4D4D;
    height: 52px !important;
    width: 150px;
    font-size: 30px;
    border-radius: 12px;
}
.logo{
    cursor: pointer !important;
}

.cart-text{
    font-size: 22px ;
    font-family: initial ;
}
.del-font{
    font-size: 22px;
}
.btn-icon{
    font-size: 22px !important;
}
.v-btn{
    background-color: #836CA9 !important;
    color: white !important;
    font-size: 20px !important;
}